const common = {
    type: "",
    show: false,
    style: {},
    autoClose: true
};

const defaultState = {
    listSelect: {
        ...common,
        title: "",
        list: ["1", "2", "..."],
        onselect: index => { }, // 
    },
    groupEdit: {
        ...common,
        group: {},
        onsubmit: group => { }, // 
    },
    contactAdd: {
        ...common,
        title: "",
        hint: "",
        list: [{ text: "1", value: "one" }, { text: "2", value: "two" }, "..."],
        onsubmit: ({ option, content }) => { }, // , option  value 
        oncancel: () => { }, // 
    }
};

const state = { ...defaultState };

const actions = {
    show: function () {

    },
    hide: function () {

    }
};

const mutations = {
    show: function (state, payload) {
        state[payload.type] = Object.assign({}, defaultState[payload.type], payload, { show: true });
    },
    hide: function (state, payload) {
        // 
        if (!payload) {
            for (let prop in state) {
                if (state[prop].show) {
                    state[prop].show = false;
                }
            }
        } else { // 
            state[payload.type] = { show: false };
        }
    }
};

const layer = {
    namespaced: true,
    state,
    actions,
    mutations
};

export {
    layer
}


